.navigation__logo {
	width: 25%;
	float: left;
	@include r(950) {
		width: 80%;
		text-align: center; } }

.navigation__list {
	width: 53%;
	float: left;
	padding: 0px 35px;
	@include r(950) {
		display: none; } }

.action-buttons {
	width: 22%;
	float: left;
	@include r(950) {
		display: none; } }

.main-app__about {
	width: 50%;
	float: left;
	padding: 29px 30px 29px 0px;
	@include r(950) {
		width: 100%;
		text-align: center;
		padding: 0px 15px; } }



.main-app__features {
	width: 50%;
	float: left;
	padding-left: 5px;
	@include r(950) {
		width: 100%;
		margin: 25px 0px 0px;
		padding-left: 0; } }

.main-app__features-list {
	float: left;
	width: 34%;
	padding-top: 71px;
	@include r(1060) {
		width: 18%; }
	@include r(450) {
		width: 100%;
		padding: 0px 15px;
		display: flex;
		justify-content: center;
		margin-bottom: 50px; } }

.main-app__tabs {
	width: 50%;
	float: left; }

.app-tabs {
	width: 49.2%;
	float: left;
	border: 1px solid #dae8f2;
	border-radius: 10px;
	margin-top: 22px;
	margin-left: 7px;
	background-color: #fcfdff;
	@include r(950) {
		width: 100%;
		margin-left: 0px; } }

.specification__item {
	width: 33.3%;
	float: left;
	text-align: center;
	position: relative;
	&::after {
		content: '';
		position: absolute;
		right: -3px;
		top: 50px;
		width: 2px;
		height: 80px;
		background-color: #3a4249; }
	&:last-child::after {
		display: none; }
	@include r(678) {
		width: 100%;
		margin: 15px 0px;
		&::after {
			display: none; } } }

.performance__list-item {
    width: 120px;
    height: 121px;
    float: left;
    border: 2px solid #5374a5;
    border-radius: 17px;
    padding: 2px;
    margin: 20px 19px;
    transition: all .5s;
    &:hover {
    	border: 2px solid #426985; }
    @include r(950) {
    	margin: 20px 10px; }
    @include r(580) {
    	width: 29.3%;
    	margin: 2%; }
    @include r(500) {
    	width: 45%; } }

.powerfull-app__item {
	width: 25%;
	float: left;
	margin-top: 47px;
	position: relative;
	&::after {
		content: '';
		position: absolute;
		right: -7px;
		top: 37px;
		background-image: url("../img/app-separator.png");
		width: 12px;
		height: 30px; }
	&:last-child::after {
		display: none; }
	@include r(992) {
		width: 50%;
		&:nth-child(2)::after {
			display: none; } } }

.our-clients__item {
	margin: 27px;
	width: 29.1%;
	float: left;
	position: relative;
	@include r(950) {
		width: 46%;
		margin: 10px 0;
		&:first-child {
			margin: 10px 0; }

		&:nth-child(3) {
			margin: 10px 0; }

		&:nth-child(4) {
			margin: 10px 0; }

		&:nth-child(6) {
			margin: 10px 0; } }

	@include r(580) {
		width: 100%; }

	&:first-child {
		margin-left: 0px; }

	&:nth-child(3) {
		margin-right: 0px; }

	&:nth-child(4) {
		margin-left: 0px;
		margin-right: 30px; }

	&:nth-child(6) {
		margin-left: 25px;
		margin-right: 0px; } }


.helped-events__item {
	width: 25%;
	float: left;
	position: relative;
	margin-top: 45px;
	&::after {
		content: '';
		width: 2px;
		height: 40px;
		background-color: #5b5d9c;
		position: absolute;
		right: 0;
		top: 27px; }
	&:last-child::after {
		display: none; }
	@include r(678) {
		width: 50%; }
	@include r(480) {
		width: 100%;
		&::after {
			display: none; } } }

.select-price__item {
	width: 33.3%;
	float: left;
	background-color: #fff;
	border-radius: 7px 0px 0px 7px;
	&:nth-child(2) {
		border-radius: 7px 7px 0px 0px; }
	&:last-child {
		border-radius: 0px 7px 7px 0px; }
	@include r(678) {
		width: 100%;
		&:last-child,
		&:nth-child(2) {
			border-radius: 0px; } } }


.get-started__item {
	width: 50%;
	float: left;
	@include r(678) {
		width: 100%; } }

.footer__item {
	width: 25%;
	float: left;
	margin-top: 3px;
	padding-left: 2px;
	@include r(950) {
		width: 50%;
		text-align: center;
		margin: 25px 0px; } }

.copyright__item-left {
	width: 50%;
	float: left;
	@include r(480) {
		width: 100%;
		text-align: center; } }

.copyright__item-right {
	width: 50%;
	float: right;
	text-align: right;
	color: #676f80;
	padding-right: 52px;
	@include r(480) {
		width: 100%;
		text-align: center;
		padding-right: 0;
		margin-top: 10px; } }
